import React, { Component } from 'react';
import Total from './components/Total/Total';
import History from './components/History/History';
import Operation from './components/Operation/Operation';

class App extends Component {
  state = {
    transactions: JSON.parse(localStorage.getItem('calcMoney')) || [],
    description: '',
    amount: '',
    balance: 0,
    earn: 0,
    expend: 0
  }

  addTransaction = (add) => {
    const transactions = [...this.state.transactions];

    const transaction = {
      id: `cmr${(+new Date).toString(16)}`,
      description: this.state.description,
      amount: this.state.amount,
      add
    }
    this.changeBalance(add, transaction.amount);
    transactions.push(transaction);

    this.setState({ transactions, description: '', amount: '' }, () => {
      this.addStorage();
      this.changeBalance(this.add, this.amount);
    });
  }

  addAmount = (e) => {
    this.setState({ amount: e.target.value })
  }

  changeBalance = (add, amount) => {
    let bal = this.state.balance;
    let earn = this.state.earn;
    let expend = this.state.expend;
    if (add) {
      bal += Number(amount);
      earn += Number(amount);
    } else {
      bal -= Number(amount);
      expend += Number(amount);
    }
    this.setState({ balance: bal, earn: earn, expend: expend })
  }

  addDescription = (e) => {
    this.setState({ description: e.target.value })
  }

  addStorage = () => {
    localStorage.setItem('calcMoney', JSON.stringify(this.state.transactions))
  }


  render() {
    return (
      <React.Fragment>
        <header>
          <h1>Кошелек</h1>
          <h2>Калькулятор расходов</h2>
        </header>

        <main>
          <div className="container">
            <Total balance={this.state.balance} earn={this.state.earn} expend={this.state.expend} />
            <History transactions={this.state.transactions} />
            <Operation addTransaction={this.addTransaction} addAmount={this.addAmount} addDescription={this.addDescription} description={this.state.description}
              amount={this.state.amount} changeBalance={this.changeBalance} />
          </div>
        </main>
      </React.Fragment>
    );
  }
}

export default App;
